    top: 50%;
    left: 50%;
    transform: translate(-50%, 0%);
  }
#toasta .toast {
    cursor: pointer;
    margin: 0 0 6px;
    padding: 0;
    width: 285px;
    height: 65px;
    display: table;
    background-size: 24px 24px;
    background-position: 15px center;
    background-repeat: no-repeat;
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.25);
    opacity: 0.9;
